import { mapActions } from 'vuex'

export default {
	props: ['tab'],
	data() {
		return {
			formData: {
				name: '',
				email: '',
				password: ''
			}
		}
	},
	methods: {
		...mapActions('store', ['registerUser', 'loginUser']),
		submitForm() {
			if (this.tab == 'login') {
				this.loginUser(this.formData)
			}
			else {
				this.registerUser(this.formData)
			}
		}
	}
}
